<template>
  <v-dialog
    v-model="dialog"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        icon
        v-bind="attrs"
        v-on="on"
        @click="getItems()"
      >
        <v-icon>mdi-cart</v-icon>
      </v-btn>
    </template>
    <v-card class="tg-bg" loading="$apollo.loading">
      <v-toolbar
        color="primary"
      >
        <v-btn
          icon
          @click="dialog = false"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Cart</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn
            text
            @click="sendOrder()"
          >
            Buy
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <CartItems :items="items" />

    </v-card>
  </v-dialog>
</template>

<script>
import CartItems from './CartItems.vue'

import gql from 'graphql-tag'

export default {
  name: 'DialogItem',

  components: {
    CartItems
  },

  data: () => ({
    dialog: false,
    items: []
  }),

  methods: {
    async sendOrder() {
      await this.$apollo.mutate({
        mutation: gql`mutation ($orderProducts: [OrderProduct!]!) {
          createOrder(orderProducts: $orderProducts) {
            id
          }
        }`,
        variables: {
          orderProducts: this.$store.state.selectedItems
        }
      })
    },

    async getItems() {
      const res = await this.$apollo.query({
        query: gql`query ($ids: [Int!]!) {
          products: productsByIds(ids: $ids) {
            id
            title: name
            price
            imageUrl
          }
        }`,
        variables: {
          ids: this.$store.state.selectedItems.map(item => item.id)
        }
      })
      this.items = res.data.products
    }
  },
}
</script>

