import { ApolloClient } from 'apollo-client'
import { createHttpLink } from 'apollo-link-http'
import { InMemoryCache } from 'apollo-cache-inmemory'

let uri = window.location.search.substring(1)
let params = new URLSearchParams(uri)

const httpLink = createHttpLink({
  uri: `http://localhost:8000/graphql?shop_id=${params.get("shop")}`,
  headers: {
    'Authorization': window.Telegram?.WebApp.initData || 'query_id=AAFev0cdAAAAAF6_Rx0i9Gra&user=%7B%22id%22%3A491241310%2C%22first_name%22%3A%22%D0%92%D0%BE%D0%BB%D0%BE%D0%B4%D0%B8%D0%BC%D0%B8%D1%80%22%2C%22last_name%22%3A%22%22%2C%22language_code%22%3A%22ru%22%7D&auth_date=1656838127&hash=e6a357d2dec3c23b16ec9b5b87a7367ab1e1ceb621684af5b862173407d78c3d'
  }
})

const cache = new InMemoryCache()

const apolloClient = new ApolloClient({
  link: httpLink,
  cache,
})

export default apolloClient
