<template>
  <div>
    <v-badge
      class="ma-2"
      color="primary"
      :value="$store.getters.getItem(id)"
      :content="$store.getters.getItem(id)"
      overlap
    >
      <v-card
        max-width="160"
        flat
      >
        <v-list-item-avatar
          tile
          size="160"
          class="pt-0 ma-0"
        >
          <v-img
            :src="url"
            height="160"
            width="160"
            class="ma-0"
            @click="dialog = true"
          ></v-img>
        </v-list-item-avatar>

        <div
          class="px-3 pt-3 text-truncate"
          @click="dialog = true"
        >{{ title }}</div>

        <v-card-actions class="pt-1">
          <div class="ml-2" color="secondary">
            ${{ price }}
          </div>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            v-if="!$store.getters.getItem(id)"
            @click="$store.commit('setItem', {id: id, val: 1})"
          >
            ADD
          </v-btn>
          <div v-else class="d-flex d-inline">
            <v-fade-transition
              v-show="selected"
            >
              <div>
                <v-btn
                  color="secondary"
                  @click="$store.commit('addItem', {id: id, val: -1})"
                  fab
                  x-small
                >
                  <v-icon>mdi-minus</v-icon>
                </v-btn>
                <v-btn
                  class="ml-1"
                  color="primary"
                  @click="$store.commit('addItem', {id: id, val: 1})"
                  fab
                  x-small
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </div>
            </v-fade-transition>
          </div>
        </v-card-actions>
      </v-card>
    </v-badge>

    <v-dialog
      v-model="dialog"
      fullscreen
    >
      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-toolbar-items>
          </v-toolbar-items>
        </v-toolbar>

        <v-card-title class="text-h5">
          <div>{{ title }}</div>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            v-if="!$store.getters.getItem(id)"
            @click="$store.commit('setItem', {id: id, val: 1})"
          >
            ADD
          </v-btn>
          <div v-else class="d-flex d-inline">
            <v-fade-transition
              v-show="selected"
            >
              <div>
                <v-btn
                  color="secondary"
                  @click="$store.commit('addItem', {id: id, val: -1})"
                  fab
                  x-small
                >
                  <v-icon>mdi-minus</v-icon>
                </v-btn>
                <v-btn
                  class="ml-1"
                  color="primary"
                  @click="$store.commit('addItem', {id: id, val: 1})"
                  fab
                  x-small
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </div>
            </v-fade-transition>
          </div>
        </v-card-title>

        <v-card-text>
          {{ description }}
        </v-card-text>

        <v-card-actions class="pt-1">
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "CardItem",

  props: {
    id: {
      type: Number,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    url: {
      type: String,
    },
    price: {
      type: String,
      required: true
    },
  },

  data: () => ({
    selected: 0,
    dialog: false
  }),

  created() {
    this.selected = this.$store.getters.getItem(this.id)
  },

  watch: {
    selected() {
      this.$store.commit('setItem', {id: this.id, val: this.selected})
    }
  }
}
</script>

